import { MongooseProduct } from "@/api/products/model/product.schema";


/**검색 기능 */
export class SearchModel{
    async searchProducts(keyword:string):Promise<any>{
        try{
            //제품명에 검색
            const products = await MongooseProduct.find({
                $or:[
                    {
                        productName:{$regex:keyword, $options:'i'} //제품명에서 검색
                     }
                ]
            });
            return products;
        }catch(error){
            console.log('제품명을 찾을 수 없음.',error)
            throw new Error('search 실패')
        }
    }
}